html {
  min-height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: url(/img/mario-bg.png) no-repeat;
  background-size: 100%;
  background-position: bottom;
  background-color: #95e8f3;
  min-height: 100%
}

form {
  padding: 20px;
  margin-top: 60px;
}

form button, form h5 {
  margin: 20px, 0;
}

input[type=text]:not(.browswer-default):focus:not([readonly]),
input[type=email]:not(.browswer-default):focus:not([readonly]),
input[type=password]:not(.browswer-default):focus:not([readonly]),
textarea.materialize-textarea:focus:not([readonly]) {
  border-color: #ec407a;
  -webkit-box-shadow: none;
          box-shadow: none;
}



